/**
 * UI Store - Zustand Implementation
 * 
 * Centralized UI state management including tabs, modals, forms,
 * loading states, and error states. Consolidates scattered useState
 * patterns into a unified store.
 * 
 * Created: January 16, 2025
 * Status: âœ… ACTIVE
 */

import { create } from 'zustand';
import { devtools } from 'zustand/middleware';
import { persist } from 'zustand/middleware';
import { immer } from 'zustand/middleware/immer';
import { enableMapSet } from 'immer';

import { withOptional } from '@/lib/utils/objects';
import { logger } from '@/lib/utils/logger';

import type { BaseStore } from './types';

// Enable Immer MapSet plugin for Set/Map support
enableMapSet();

// UI store state interface
type UIStore = {
  // Tab Management
  activeTab: string;
  tabHistory: string[];
  setActiveTab: (tab: string) => void;
  goBackTab: () => void;
  clearTabHistory: () => void;
  
  // Modal Management
  activeModal: string | null;
  modalData: any;
  modalStack: Array<{
    id: string;
    data: any;
    timestamp: number;
  }>;
  openModal: (id: string, data?: any) => void;
  closeModal: () => void;
  closeAllModals: () => void;
  
  // Form State Management
  formData: Record<string, any>;
  formErrors: Record<string, string | null>;
  formTouched: Record<string, boolean>;
  setFormData: (key: string, value: any) => void;
  setFormError: (key: string, error: string | null) => void;
  setFormTouched: (key: string, touched: boolean) => void;
  clearForm: (key?: string) => void;
  validateForm: (key: string) => boolean;
  
  // Loading States
  loadingStates: Record<string, boolean>;
  setLoading: (key: string, loading: boolean) => void;
  isLoading: (key: string) => boolean;
  clearLoading: (key: string) => void;
  clearAllLoading: () => void;
  
  // Error States
  errors: Record<string, string | null>;
  setError: (key: string, error: string | null) => void;
  clearError: (key: string) => void;
  clearAllErrors: () => void;
  hasError: (key: string) => boolean;
  
  // Toast/Notification States
  toasts: Array<{
    id: string;
    type: 'success' | 'error' | 'warning' | 'info';
    message: string;
    duration?: number;
    timestamp: number;
  }>;
  addToast: (toast: Omit<UIStore['toasts'][0], 'id' | 'timestamp'>) => void;
  removeToast: (id: string) => void;
  clearToasts: () => void;
  
  // Sidebar/Drawer States
  sidebarOpen: boolean;
  drawerOpen: boolean;
  toggleSidebar: () => void;
  toggleDrawer: () => void;
  setSidebarOpen: (open: boolean) => void;
  setDrawerOpen: (open: boolean) => void;
  
  // Search States
  searchQuery: string;
  searchResults: any[];
  searchLoading: boolean;
  setSearchQuery: (query: string) => void;
  setSearchResults: (results: any[]) => void;
  setSearchLoading: (loading: boolean) => void;
  clearSearch: () => void;
  
  // Filter States
  filters: Record<string, any>;
  setFilter: (key: string, value: any) => void;
  clearFilter: (key: string) => void;
  clearAllFilters: () => void;
  
  // Pagination States
  pagination: Record<string, {
    page: number;
    limit: number;
    total: number;
    hasMore: boolean;
  }>;
  setPagination: (key: string, pagination: Partial<UIStore['pagination'][string]>) => void;
  nextPage: (key: string) => void;
  prevPage: (key: string) => void;
  resetPagination: (key: string) => void;
  
  // Selection States
  selections: Record<string, Set<string>>;
  selectItem: (key: string, itemId: string) => void;
  deselectItem: (key: string, itemId: string) => void;
  toggleSelection: (key: string, itemId: string) => void;
  clearSelection: (key: string) => void;
  clearAllSelections: () => void;
  isSelected: (key: string, itemId: string) => boolean;
  
  // View States
  viewMode: Record<string, 'grid' | 'list' | 'table'>;
  setViewMode: (key: string, mode: 'grid' | 'list' | 'table') => void;
  
  // Sort States
  sortBy: Record<string, string>;
  sortOrder: Record<string, 'asc' | 'desc'>;
  setSort: (key: string, by: string, order?: 'asc' | 'desc') => void;
  toggleSort: (key: string, by: string) => void;
  
  // Utility Actions
  resetUI: () => void;
  resetSection: (section: keyof UIStore) => void;
} & BaseStore;

// Create UI store with middleware
export const useUIStore = create<UIStore>()(
  devtools(
    persist(
      immer((set, get) => ({
        // Initial state
        activeTab: 'feed',
        tabHistory: ['feed'],
        
        // Modal state
        activeModal: null,
        modalData: null,
        modalStack: [],
        
        // Form state
        formData: {},
        formErrors: {},
        formTouched: {},
        
        // Loading states
        loadingStates: {},
        
        // Error states
        errors: {},
        
        // Toast states
        toasts: [],
        
        // Sidebar/Drawer states
        sidebarOpen: false,
        drawerOpen: false,
        
        // Search states
        searchQuery: '',
        searchResults: [],
        searchLoading: false,
        
        // Filter states
        filters: {},
        
        // Pagination states
        pagination: {},
        
        // Selection states
        selections: {},
        
        // View states
        viewMode: {},
        
        // Sort states
        sortBy: {},
        sortOrder: {},
        
        // Base store state
        baseLoading: false,
        error: null,
        
        // Base store actions
        setBaseLoading: (loading) => set((state) => {
          state.baseLoading = loading;
        }),
        
        setBaseError: (error) => set((state) => {
          state.error = error;
        }),
        
        clearBaseError: () => set((state) => {
          state.error = null;
        }),
        
        // Tab actions
        setActiveTab: (tab) => set((state) => {
          if (state.activeTab !== tab) {
            state.tabHistory.push(state.activeTab);
            state.activeTab = tab;
            logger.info('Tab changed', { from: state.tabHistory[state.tabHistory.length - 1], to: tab });
          }
        }),
        
        goBackTab: () => set((state) => {
          if (state.tabHistory.length > 0) {
            const previousTab = state.tabHistory.pop();
            if (previousTab) {
              state.activeTab = previousTab;
              logger.info('Tab went back', { to: previousTab });
            }
          }
        }),
        
        clearTabHistory: () => set((state) => {
          state.tabHistory = [];
        }),
        
        // Modal actions
        openModal: (id, data) => set((state) => {
          state.activeModal = id;
          state.modalData = data;
          state.modalStack.push({
            id,
            data,
            timestamp: Date.now()
          });
          logger.info('Modal opened', { id, data });
        }),
        
        closeModal: () => set((state) => {
          if (state.modalStack.length > 0) {
            state.modalStack.pop();
            if (state.modalStack.length > 0) {
              const previousModal = state.modalStack[state.modalStack.length - 1];
              state.activeModal = previousModal.id;
              state.modalData = previousModal.data;
            } else {
              state.activeModal = null;
              state.modalData = null;
            }
          } else {
            state.activeModal = null;
            state.modalData = null;
          }
          logger.info('Modal closed');
        }),
        
        closeAllModals: () => set((state) => {
          state.activeModal = null;
          state.modalData = null;
          state.modalStack = [];
          logger.info('All modals closed');
        }),
        
        // Form actions
        setFormData: (key, value) => set((state) => {
          state.formData[key] = value;
          // Clear error when user starts typing
          if (state.formErrors[key]) {
            state.formErrors[key] = null;
          }
        }),
        
        setFormError: (key, error) => set((state) => {
          state.formErrors[key] = error;
        }),
        
        setFormTouched: (key, touched) => set((state) => {
          state.formTouched[key] = touched;
        }),
        
        clearForm: (key) => set((state) => {
          if (key) {
            delete state.formData[key];
            delete state.formErrors[key];
            delete state.formTouched[key];
          } else {
            state.formData = {};
            state.formErrors = {};
            state.formTouched = {};
          }
        }),
        
        validateForm: (key) => {
          const state = get();
          return !state.formErrors[key] && state.formTouched[key];
        },
        
        // Loading actions
        setLoading: (key, loading) => set((state) => {
          state.loadingStates[key] = loading;
        }),
        
        isLoading: (key) => {
          const state = get();
          return state.loadingStates[key] || false;
        },
        
        clearLoading: (key) => set((state) => {
          delete state.loadingStates[key];
        }),
        
        clearAllLoading: () => set((state) => {
          state.loadingStates = {};
        }),
        
        // Error actions
        setError: (key, error) => set((state) => {
          state.errors[key] = error;
        }),
        
        clearError: (key) => set((state) => {
          delete state.errors[key];
        }),
        
        clearAllErrors: () => set((state) => {
          state.errors = {};
        }),
        
        hasError: (key) => {
          const state = get();
          return !!state.errors[key];
        },
        
        // Toast actions
        addToast: (toast) => set((state) => {
          const id = `toast-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
          state.toasts.push({
            ...toast,
            id,
            timestamp: Date.now()
          });
          logger.info('Toast added', { id, type: toast.type, message: toast.message });
        }),
        
        removeToast: (id) => set((state) => {
          state.toasts = state.toasts.filter(toast => toast.id !== id);
        }),
        
        clearToasts: () => set((state) => {
          state.toasts = [];
        }),
        
        // Sidebar/Drawer actions
        toggleSidebar: () => set((state) => {
          state.sidebarOpen = !state.sidebarOpen;
        }),
        
        toggleDrawer: () => set((state) => {
          state.drawerOpen = !state.drawerOpen;
        }),
        
        setSidebarOpen: (open) => set((state) => {
          state.sidebarOpen = open;
        }),
        
        setDrawerOpen: (open) => set((state) => {
          state.drawerOpen = open;
        }),
        
        // Search actions
        setSearchQuery: (query) => set((state) => {
          state.searchQuery = query;
        }),
        
        setSearchResults: (results) => set((state) => {
          state.searchResults = results;
        }),
        
        setSearchLoading: (loading) => set((state) => {
          state.searchLoading = loading;
        }),
        
        clearSearch: () => set((state) => {
          state.searchQuery = '';
          state.searchResults = [];
          state.searchLoading = false;
        }),
        
        // Filter actions
        setFilter: (key, value) => set((state) => {
          state.filters[key] = value;
        }),
        
        clearFilter: (key) => set((state) => {
          delete state.filters[key];
        }),
        
        clearAllFilters: () => set((state) => {
          state.filters = {};
        }),
        
        // Pagination actions
        setPagination: (key, pagination) => set((state) => {
          state.pagination[key] = {
            page: 1,
            limit: 10,
            total: 0,
            hasMore: false,
            ...state.pagination[key],
            ...pagination
          };
        }),
        
        nextPage: (key) => set((state) => {
          if (state.pagination[key]) {
            state.pagination[key].page += 1;
          }
        }),
        
        prevPage: (key) => set((state) => {
          if (state.pagination[key] && state.pagination[key].page > 1) {
            state.pagination[key].page -= 1;
          }
        }),
        
        resetPagination: (key) => set((state) => {
          if (state.pagination[key]) {
            state.pagination[key].page = 1;
          }
        }),
        
        // Selection actions
        selectItem: (key, itemId) => set((state) => {
          if (!state.selections[key]) {
            state.selections[key] = new Set();
          }
          state.selections[key].add(itemId);
        }),
        
        deselectItem: (key, itemId) => set((state) => {
          if (state.selections[key]) {
            state.selections[key].delete(itemId);
          }
        }),
        
        toggleSelection: (key, itemId) => set((state) => {
          if (!state.selections[key]) {
            state.selections[key] = new Set();
          }
          if (state.selections[key].has(itemId)) {
            state.selections[key].delete(itemId);
          } else {
            state.selections[key].add(itemId);
          }
        }),
        
        clearSelection: (key) => set((state) => {
          if (state.selections[key]) {
            state.selections[key].clear();
          }
        }),
        
        clearAllSelections: () => set((state) => {
          state.selections = {};
        }),
        
        isSelected: (key, itemId) => {
          const state = get();
          return state.selections[key]?.has(itemId) || false;
        },
        
        // View actions
        setViewMode: (key, mode) => set((state) => {
          state.viewMode[key] = mode;
        }),
        
        // Sort actions
        setSort: (key, by, order = 'asc') => set((state) => {
          state.sortBy[key] = by;
          state.sortOrder[key] = order;
        }),
        
        toggleSort: (key, by) => set((state) => {
          if (state.sortBy[key] === by) {
            state.sortOrder[key] = state.sortOrder[key] === 'asc' ? 'desc' : 'asc';
          } else {
            state.sortBy[key] = by;
            state.sortOrder[key] = 'asc';
          }
        }),
        
        // Utility actions
        resetUI: () => set((state) => {
          state.activeTab = 'feed';
          state.tabHistory = ['feed'];
          state.activeModal = null;
          state.modalData = null;
          state.modalStack = [];
          state.formData = {};
          state.formErrors = {};
          state.formTouched = {};
          state.loadingStates = {};
          state.errors = {};
          state.toasts = [];
          state.sidebarOpen = false;
          state.drawerOpen = false;
          state.searchQuery = '';
          state.searchResults = [];
          state.searchLoading = false;
          state.filters = {};
          state.pagination = {};
          state.selections = {};
          state.viewMode = {};
          state.sortBy = {};
          state.sortOrder = {};
          state.baseLoading = false;
          state.error = null;
        }),
        
        resetSection: (section) => set((state) => {
          switch (section) {
            case 'formData':
              state.formData = {};
              state.formErrors = {};
              state.formTouched = {};
              break;
            case 'loadingStates':
              state.loadingStates = {};
              break;
            case 'errors':
              state.errors = {};
              break;
            case 'toasts':
              state.toasts = [];
              break;
            case 'filters':
              state.filters = {};
              break;
            case 'pagination':
              state.pagination = {};
              break;
            case 'selections':
              state.selections = {};
              break;
            default:
              logger.warn('Unknown section to reset', { section });
          }
        }),
      })),
      {
        name: 'ui-store',
        partialize: (state) => ({
          activeTab: state.activeTab,
          sidebarOpen: state.sidebarOpen,
          viewMode: state.viewMode,
          sortBy: state.sortBy,
          sortOrder: state.sortOrder,
        }),
      }
    ),
    {
      name: 'ui-store',
    }
  )
);

// Export optimized selectors
export const useActiveTab = () => useUIStore(state => state.activeTab);
export const useSetActiveTab = () => useUIStore(state => state.setActiveTab);
export const useActiveModal = () => useUIStore(state => state.activeModal);
export const useModalData = () => useUIStore(state => state.modalData);
export const useFormData = (key: string) => useUIStore(state => state.formData[key]);
export const useFormError = (key: string) => useUIStore(state => state.formErrors[key]);
export const useFormTouched = (key: string) => useUIStore(state => state.formTouched[key]);
export const useLoading = (key: string) => useUIStore(state => state.loadingStates[key] || false);
export const useError = (key: string) => useUIStore(state => state.errors[key]);
export const useToasts = () => useUIStore(state => state.toasts);
export const useSidebarOpen = () => useUIStore(state => state.sidebarOpen);
export const useDrawerOpen = () => useUIStore(state => state.drawerOpen);
export const useSearchQuery = () => useUIStore(state => state.searchQuery);
export const useSearchResults = () => useUIStore(state => state.searchResults);
export const useSearchLoading = () => useUIStore(state => state.searchLoading);
export const useFilters = (key: string) => useUIStore(state => state.filters[key]);
export const usePagination = (key: string) => useUIStore(state => state.pagination[key]);
export const useSelections = (key: string) => useUIStore(state => state.selections[key]);
export const useViewMode = (key: string) => useUIStore(state => state.viewMode[key]);
export const useSortBy = (key: string) => useUIStore(state => state.sortBy[key]);
export const useSortOrder = (key: string) => useUIStore(state => state.sortOrder[key]);

// Export action selectors
export const useUIActions = () => useUIStore(state => ({
  setActiveTab: state.setActiveTab,
  goBackTab: state.goBackTab,
  clearTabHistory: state.clearTabHistory,
  openModal: state.openModal,
  closeModal: state.closeModal,
  closeAllModals: state.closeAllModals,
  setFormData: state.setFormData,
  setFormError: state.setFormError,
  setFormTouched: state.setFormTouched,
  clearForm: state.clearForm,
  validateForm: state.validateForm,
  setLoading: state.setLoading,
  clearLoading: state.clearLoading,
  clearAllLoading: state.clearAllLoading,
  setError: state.setError,
  clearError: state.clearError,
  clearAllErrors: state.clearAllErrors,
  addToast: state.addToast,
  removeToast: state.removeToast,
  clearToasts: state.clearToasts,
  toggleSidebar: state.toggleSidebar,
  toggleDrawer: state.toggleDrawer,
  setSidebarOpen: state.setSidebarOpen,
  setDrawerOpen: state.setDrawerOpen,
  setSearchQuery: state.setSearchQuery,
  setSearchResults: state.setSearchResults,
  setSearchLoading: state.setSearchLoading,
  clearSearch: state.clearSearch,
  setFilter: state.setFilter,
  clearFilter: state.clearFilter,
  clearAllFilters: state.clearAllFilters,
  setPagination: state.setPagination,
  nextPage: state.nextPage,
  prevPage: state.prevPage,
  resetPagination: state.resetPagination,
  selectItem: state.selectItem,
  deselectItem: state.deselectItem,
  toggleSelection: state.toggleSelection,
  clearSelection: state.clearSelection,
  clearAllSelections: state.clearAllSelections,
  isSelected: state.isSelected,
  setViewMode: state.setViewMode,
  setSort: state.setSort,
  toggleSort: state.toggleSort,
  resetUI: state.resetUI,
  resetSection: state.resetSection,
}));
